@keyframes rotate-seconds {
  from {
    transform: translate(-50%, -100%)rotate(0);
  }

  to {
    transform: translate(-50%, -100%)rotate(360deg);
  }
}

@keyframes rotate-hours {
  from {
    transform: translate(-50%, -100%)rotate(0);
  }

  to {
    transform: translate(-50%, -100%)rotate(360deg);
  }
}

body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  display: flex;
}

.stopwatch__center {
  z-index: 1;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  display: flex;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.stopwatch__minute-hand {
  z-index: 0;
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate-hours;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
}

.stopwatch__second-hand {
  z-index: 0;
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate-seconds;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
}

.stopwatch--speed-up .stopwatch__minute-hand {
  animation-duration: 600s;
}

.stopwatch--speed-up .stopwatch__second-hand {
  animation-duration: 10s;
}
